import { useLocation, useNavigate } from 'react-router-dom';
import 'react-datepicker/dist/react-datepicker.css';
import { useState, useEffect, useRef } from 'react';
import 'assets/css/batteriesAddOrUpdate.css';
import { useDispatch, useSelector } from 'react-redux';
import { DetailTitleBar } from 'app/theme-layouts/mainLayout/components';
import { open, close } from 'app/store/common/loadingWrap';
import { confirmAlert } from 'react-confirm-alert';
import { showMessage } from 'app/store/fuse/messageSlice';
import 'react-confirm-alert/src/react-confirm-alert.css';
import { selectComboBox } from 'app/store/common/comboBox';
import { selectDepartment, setDepartment } from 'app/store/common/department';

function AddOrUpdate() {
  const dispatch = useDispatch();
  const companyComboBox = useSelector(selectComboBox).company;
  const projectComboBox = useSelector(selectComboBox).project;
  const [projectId, setProjectId] = useState('');
  const [companyId, setCompanyId] = useState('');
  const [projectCategory, setProjectCategory] = useState([]);
  const [companyCategory, setCompanyCategory] = useState([]);
  const [validation, setValidation] = useState([]);
  const nameInput = useRef();
  const useYn = useRef();
  const desc = useRef();
  const navigate = useNavigate();
  const location = useLocation();
  const param = location.state;
  const dataList = useSelector(selectDepartment).department;

  const saveBtnClick = () => {
    confirmAlert({
      title: `부서 정보를 ${param.detailLink[0].title} 하시겠습니까?`,
      buttons: [
        {
          label: '예',
          onClick: () => {
            dispatch(open());
            const departmentObj = {};
            departmentObj.projectId = projectId;
            departmentObj.companyId = companyId;
            departmentObj.deptName = nameInput.current.value;
            departmentObj.useYn = useYn.current.value;
            departmentObj.deptDesc = desc.current.value;
            const filter1 = dataList.filter((obj) => obj.projectId === projectId);
            const filter2 = filter1.filter((obj) => obj.deptName === nameInput.current.value);
            if (filter2.length > 0) {
              dispatch(showMessage({ message: '부서 정보 저장 실패.', variant: 'error' }));
              setValidation([{ type: 'deptName', message: '이미 존재하는 부서명입니다.' }]);
              dispatch(close());
              return;
            }
            const today = new Date();
            const year = today.getFullYear();
            const month = `0${today.getMonth() + 1}`.slice(-2);
            const day = `0${today.getDate()}`.slice(-2);
            const dateString = `${year}-${month}-${day}`;
            const hours = `0${today.getHours()}`.slice(-2);
            const minutes = `0${today.getMinutes()}`.slice(-2);
            const seconds = `0${today.getSeconds()}`.slice(-2);
            const timeString = `${hours}:${minutes}:${seconds}`;
            const clone = dataList.slice();
            const filter = clone.filter((obj) => obj.deptId !== param.deptId);
            switch (param.detailLink[0].title) {
              case '추가':
                clone.push({
                  companyId,
                  companyName: '(주)한빛',
                  deptId: String(Number(dataList[dataList.length - 1].deptId) + 1),
                  deptName: nameInput.current.value,
                  deptDesc: desc.current.value,
                  projectId,
                  projectName: projectComboBox.filter((obj) => obj.code === projectId)[0].codeName,
                  memberCnt: 0,
                  useYn: useYn.current.value,
                  createUsername: '한빛 관리자',
                  createTime: `${dateString} ${timeString}`,
                  updateUsername: '한빛 관리자',
                  updateTime: `${dateString} ${timeString}`,
                });
                dispatch(close());
                dispatch(setDepartment({ data: clone }));
                dispatch(
                  showMessage({ message: '부서 정보가 추가되었습니다.', variant: 'success' })
                );
                navigate('/bss_server/systemManagement/department');
                break;
              case '수정':
                filter.push({
                  companyId,
                  companyName: '(주)한빛',
                  deptId: param.deptId,
                  deptName: nameInput.current.value,
                  deptDesc: desc.current.value,
                  projectId,
                  projectName: projectComboBox.filter((obj) => obj.code === projectId)[0].codeName,
                  memberCnt: 0,
                  useYn: useYn.current.value,
                  createUsername: '한빛 관리자',
                  createTime: param.createTime,
                  updateUsername: '한빛 관리자',
                  updateTime: `${dateString} ${timeString}`,
                });
                dispatch(close());
                dispatch(setDepartment({ data: filter }));
                dispatch(
                  showMessage({ message: '부서 정보가 수정되었습니다.', variant: 'success' })
                );
                navigate('/bss_server/systemManagement/department');
                break;
              default:
            }
          },
        },
        {
          label: '취소',
        },
      ],
    });
  };

  const deleteBtnClick = () => {
    confirmAlert({
      title: `부서 정보를 삭제 하시겠습니까?`,
      buttons: [
        {
          label: '예',
          onClick: () => {
            dispatch(open());
            const clone = dataList.slice();
            const filter = clone.filter((obj) => obj.deptId !== param.deptId);
            dispatch(close());
            dispatch(setDepartment({ data: filter }));
            dispatch(showMessage({ message: '그룹 정보가 삭제되었습니다.', variant: 'success' }));
            navigate('/bss_server/systemManagement/department');
          },
        },
        {
          label: '취소',
        },
      ],
    });
  };

  useEffect(() => {
    setCompanyCategory(companyComboBox);
    if (param.detailLink[0].title === '추가') {
      setCompanyId(companyComboBox[0].code);
      const projectArr = projectComboBox.filter((obj) => obj.parentId === companyComboBox[0].code);
      setProjectCategory(projectArr);
      setProjectId(projectArr[0].code);
    } else if (param.detailLink[0].title === '수정') {
      setCompanyId(param.companyId);
      setProjectCategory(projectComboBox.filter((obj) => obj.parentId === param.companyId));
      setProjectId(param.projectId);
    }
  }, [param.detailLink[0].title, param.companyId, param.projectId]);

  return (
    <div className="section__grid__wrap content batteries__add__update depart__manage">
      <DetailTitleBar
        flag={param.detailLink[0].title}
        deleteBtnClick={deleteBtnClick}
        saveBtnClick={saveBtnClick}
      />
      {/* input__wrap */}
      <section className="input__wrap">
        <div className="section__inner">
          <ul className="selector__wrap">
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  회사명
                </dt>
                <dd>
                  <select
                    value={companyId}
                    className="custom__select"
                    onChange={(e) => {
                      setCompanyId(e.target.value);
                      setProjectCategory(
                        projectComboBox.filter((obj) => obj.parentId === e.target.value)
                      );
                      setProjectId(projectCategory[0].code);
                    }}
                  >
                    {companyCategory.map((obj, idx) => (
                      <option key={obj.code} value={obj.code}>
                        {obj.codeName}
                      </option>
                    ))}
                  </select>
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  프로젝트명
                </dt>
                <dd>
                  <select
                    value={projectId}
                    className="custom__select"
                    onChange={(evt) => {
                      setProjectId(evt.target.value);
                    }}
                  >
                    {projectCategory.map((obj, idx) => (
                      <option key={obj.code} value={obj.code}>
                        {obj.codeName}
                      </option>
                    ))}
                  </select>
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>부서명</dt>
                <dd>
                  <input
                    ref={nameInput}
                    defaultValue={param.deptName}
                    type="text"
                    className="custom__input"
                  />
                  {validation.map(
                    (obj) =>
                      obj.type === 'deptName' && (
                        <p className="f__medium field__error">{obj.message}</p>
                      )
                  )}
                </dd>
              </dl>
            </li>
            <li className="w50">
              <dl>
                <dt>
                  <span className="f__bold" style={{ color: 'red' }}>
                    *&nbsp;
                  </span>
                  사용여부
                </dt>
                <dd>
                  <select ref={useYn} className="custom__select" defaultValue={param.useYn}>
                    <option value="1">사용</option>
                    <option value="0">미사용</option>
                  </select>
                </dd>
              </dl>
            </li>
            <li className="w100">
              <dl>
                <dt>설명</dt>
                <dd>
                  <textarea ref={desc} defaultValue={param.deptDesc} className="custom__input" />
                </dd>
              </dl>
            </li>
          </ul>
        </div>
      </section>
      {/* // input__wrap */}
    </div>
  );
}

export default AddOrUpdate;
